<template>
    <nav class="navbar navbar-expand-lg navbar-dark bg-dark mb-3">
        <div class="container-fluid">
            <a class="navbar-brand" href="#">Toko Syafin Wallpaper</a>
            <button class="navbar-toggler" type="button" data-bs-toggle="collapse"
                data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false"
                aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse" id="navbarSupportedContent">
                <ul class="navbar-nav me-auto mb-2 mb-lg-0 text-start">
                    <li class="nav-item">
                        <a class="nav-link active" aria-current="page" href="#" @click="Change($event, 'Beranda')">Beranda</a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link" href="#" @click="Change($event, 'Produk')">Produk</a>
                    </li>
                    <!-- <li class="nav-item">
                        <a class="nav-link" href="#" @click="Change($event, 'Laporan')">Laporan</a>
                    </li> -->
                    <li class="nav-item">
                        <a class="nav-link text-danger" href="#" @click="Logout">Logout</a>
                    </li>
                </ul>
            </div>
        </div>
    </nav>
</template>

<script>
    export default {
        methods: {
            Change(e, component) {
                let par = e.target.parentNode.parentNode.children
                for (let index = 0; index < par.length; index++) {
                    par[index].children[0].classList.remove('active')
                }
                e.target.classList.add('active')
                this.$parent.component = component
            },
            Logout(){
                let formData = new FormData()
                formData.append('_method', 'put')
                formData.append('hash', window.auth)
                this.axios.post(`auth`, formData)
                            .then((r) => {
                                if(r.data.status === 200) {
                                    window.auth = r.data.payload
                                    this.$router.push({ path: '/login' })
                                }
                            }).catch(() => {
                                this.$swal({
                                    position: 'center',
                                    icon: 'error',
                                    title: 'Server Error',
                                    text: 'Silakan hubungi Developer',
                                    showConfirmButton: false,
                                })
                            })
            }
        }
    }
</script>