<template>
    <div class="admin-view-products p-3 text-start">
        <button type="button" class="btn btn-success add-new-product" @click="Add"><i class="fa-solid fa-plus"></i></button>
        <table class="table">
            <thead>
                <tr>
                    <th scope="col">Nama</th>
                    <th scope="col">Gambar</th>
                    <th scope="col">Dibuat pada</th>
                    <th scope="col">Handle</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in products" :key="item.id">
                    <td class="text-start">{{ item.nama }}</td>
                    <th>
                        <div class="img-wrapper">
                            <img :src="$baseURL + 'storage/thumbnail/' + item.thumbnail">
                        </div>
                    </th>
                    <td>{{ $moment(item.created_at).locale('id').format('dddd, Do MMMM YYYY') }}</td>
                    <td>
                        <!-- <button type="button" class="btn btn-success m-1" @click="Edit(item.id)">
                            <i class="fa-solid fa-pen-to-square"></i>
                        </button> -->
                        <button type="button" class="btn btn-danger" @click="Remove(item.id)">
                            <i class="fa-solid fa-trash"></i>
                        </button>
                    </td>
                </tr>
            </tbody>
        </table>

        <div class="modal fade" id="modalProduct" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog modal-lg">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="exampleModalLabel">{{ title }}</h5>
                        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                    </div>
                    <div class="modal-body">
                        <form @submit.prevent="Store">
                            <div class="mb-3">
                                <label for="nama">Nama Produk</label>
                                <input type="text" class="form-control" id="nama" name="nama" v-model="form.nama" v-validate="'required|max:255'">
                                <div class="invalid-feedback">
                                    {{ errors.first('nama') }}
                                </div>
                            </div>
                            <div class="mb-3">
                                <label for="kuota">Kuota</label>
                                <input type="number" class="form-control" id="kuota" value="0" name="kuota" v-model="form.kuota" v-validate="'required'">
                                <div class="invalid-feedback">
                                    {{ errors.first('kuota') }}
                                </div>
                            </div>
                            <div class="mb-3">
                                <label for="keterangan">Keterangan</label>
                                <textarea class="form-control" rows="5" name="keterangan" id="keterangan" v-model="form.keterangan" v-validate="'required|max:255'"></textarea>
                                <div class="invalid-feedback">
                                    {{ errors.first('keterangan') }}
                                </div>
                            </div>
                            <div class="mb-3 text-center" v-if="form.gambar">
                                <img :src="$baseURL + 'storage/thumbnail/' + form.gambar" class="img-fluid" style="max-height: 200px;">
                            </div>
                            <template v-if="edit">
                                <div class="mb-4">
                                    <label for="gambar">Pilih Gambar</label>
                                    <input type="file" id="gambar" class="form-control" name="gambar" @change="DokumentasiPreview">
                                    <div class="invalid-feedback">
                                        {{ errors.first('gambar') }}
                                    </div>
                                </div>
                            </template>
                            <template v-else>
                                <div class="mb-4">
                                    <label for="gambar">Pilih Gambar</label>
                                    <input type="file" id="gambar" class="form-control" name="gambar" v-validate="'required'" @change="DokumentasiPreview">
                                    <div class="invalid-feedback">
                                        {{ errors.first('gambar') }}
                                    </div>
                                </div>
                            </template>
                            <button class="btn btn-success">
                                <span v-if="loading">
                                    <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                                    Loading...
                                </span>
                                <span v-else>Simpan</span>
                            </button>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import bsModal from 'bootstrap/js/dist/modal'
    
    export default {
        data() {
            return {
                edit: false,
                title: 'Tambah Produk',
                products: null,
                modal: null,
                loading: false,
                form: {
                    nama: '',
                    kuota: 0,
                    keterangan: '',
                    gambar: null,
                },
                blob: null
            }
        },
        mounted() {
            this.modal = new bsModal('#modalProduct')
            this.Get()
        },
        methods: {
            Get(){
                this.axios.get(`product`)
                        .then((r) => {
                            if(r.data.status === 200){
                                this.products = r.data.payload
                                this.loading = false
                            } else if (r.data.status === 401){
                                window.location.href = `${this.$baseURL}login`
                            }
                        })
                        .catch(() => {
                            this.$swal({
                                position: 'center',
                                icon: 'error',
                                title: 'Server Error',
                                text: 'Silakan hubungi Developer',
                                showConfirmButton: false,
                            })
                        })
            },
            Add() {
                this.title = 'Tambah Produk'
                this.ResetForm()
                this.modal.show()
            },
            Edit(id) {
                this.edit = true
                this.axios.get(`product?id=${id}`)
                        .then((r) => {
                            if(r.data.status === 200){
                                this.title = 'Edit Produk'
                                this.form.nama = r.data.payload.nama
                                this.form.kuota = r.data.payload.kuota
                                this.form.keterangan = r.data.payload.keterangan
                                this.form.gambar = r.data.payload.thumbnail
                                this.modal.show()
                                console.log(r.data.payload)
                            } else if (r.data.status === 401){
                                window.location.href = `${this.$baseURL}login`
                            }
                        })
                        .catch(() => {
                            this.$swal({
                                position: 'center',
                                icon: 'error',
                                title: 'Server Error',
                                text: 'Silakan hubungi Developer',
                                showConfirmButton: false,
                            })
                        })
            },
            Store(e){
                e.preventDefault()
                this.$validator.validate().then(valid => {
                    if (!valid) {
                        console.log('not valid')
                    } else {
                        this.loading = true
                        let formData = new FormData(e.target)
                        let config = {
                            headers: {
                                'content-type': 'multipart/form-data'
                            }
                        }
                        this.axios.post(`product`, formData, config)
                                .then((r) => {
                                    if(r.data.status === 200) {
                                        this.$swal({
                                            position: 'center',
                                            icon: 'success',
                                            title: r.data.message,
                                            showConfirmButton: false,
                                            timer: 1500
                                        })
                                        this.loading = false
                                        this.ResetForm()
                                        this.Get()
                                    } else if (r.data.status === 401){
                                        window.location.href = `${this.$baseURL}login`
                                    }
                                    
                                }).catch(() => {
                                    this.$swal({
                                        position: 'center',
                                        icon: 'error',
                                        title: 'Server Error',
                                        text: 'Silakan hubungi Developer',
                                        showConfirmButton: false,
                                    })
                                    this.loading = false
                                })
                                }
                            });
                    
            },
            Remove(id){
                let formData = new FormData()
                formData.append('_method', 'delete')
                formData.append('id', id)
                this.$swal({
                        title: 'Apakah yakin mau dihapus ?',
                        showCancelButton: true,
                        confirmButtonText: 'Ya',
                        cancelButtonText: 'Tidak',
                    }).then((result) => {
                        if (result.isConfirmed) {
                            this.axios.post(`product`, formData)
                                        .then((r) => {
                                            if(r.data.status === 200) {
                                                this.$swal({
                                                    position: 'center',
                                                    icon: 'success',
                                                    title: r.data.message,
                                                    showConfirmButton: false,
                                                    timer: 1500
                                                })
                                                this.Get()
                                            } else if (r.data.status === 401){
                                                window.location.href = `${this.$baseURL}login`
                                            }
                                            
                                        }).catch(() => {
                                            this.$swal({
                                                position: 'center',
                                                icon: 'error',
                                                title: 'Server Error',
                                                text: 'Silakan hubungi Developer',
                                                showConfirmButton: false,
                                            })
                                        })
                        } 
                    })
                
            },
            ResetForm() {
                this.modal.hide()
                this.form.nama = ''
                this.form.kuota = 0
                this.form.keterangan = ''
                document.querySelector('#modalProduct form').reset()
                this.errors.clear()
                this.$validator.reset()
            },
            DokumentasiPreview(e) {
                let file = e.target.files[0]
                this.blob = URL.createObjectURL(file)
            },
        }
    }
</script>